const initialState = {
  cartrecord: [],
};
export function reducer(state = initialState, action) {
  switch(action.type) {
    case 'ADD':
        return [
        ...state.cartrecord,
        {
          id: action.item.id,
          title: action.item.title,
          description: action.item.description,
          date: action.item.date,
          time: action.item.time,
          status: 'pending',
          created_date: action.item.created_date,
          created_time: action.item.created_time,
        }
      ]  
    case 'COPY':
      return [
        ...state.cartrecord,
        {
          id: action.item.id,
          title: action.item.title,
          description: action.item.description,
          date: action.item.date,
          time: action.item.time,
          status: action.item.status,
          created_date: action.item.created_date,
          created_time: action.item.created_time,
        }
      ]       
    case 'EDIT':
        const getrow = state.cartrecord.filter((row) => row.id === action.item.id);
        let newgetrow = getrow[0];
        newgetrow.title = action.item.title;
        newgetrow.description = action.item.description;
        newgetrow.date = action.item.date;
        newgetrow.time = action.item.time;
        return [...state.cartrecord]
    
    case 'CHANGESTATUS':
        const statusrow = state.cartrecord.filter((row) => row.id === action.item.id);
        let newstatusrow = statusrow[0];
        newstatusrow.status = action.item.status;
        return [...state.cartrecord]
    default:
      return state;
  }
}
