import React, { Suspense, lazy } from 'react';
import './App.css';
import { Redirect } from 'react-router-dom';
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import { history } from './helpers';
import PrivateRoute from './PrivateRoute';
import Header from './view/pages/Layout/Header';
import Skeleton from '@material-ui/lab/Skeleton';
import Grid from '@material-ui/core/Grid';
import Loader from 'react-loader-spinner'

const Home = lazy(() => import('./view/pages/Home/Home'));
const Addnew = lazy(() => import('./view/pages/Addnew/Addnew'));
const Edit = lazy(() => import('./view/pages/Edit/Edit'));

class App extends React.Component {

  constructor(props) {
        super(props);
  }
  
  render() {

    return (
            <Router>
                <Header/>
                <Suspense fallback={
                <Grid container>
                      <Grid item xs={1}></Grid>
                    <Grid item xs={10}>
                        <Skeleton height={700}/>
                    </Grid>
                    <Grid item xs={1}></Grid>
                </Grid>}>
                <Switch>
                    <Route path="/" exact component={Home} />
                    <Route path="/home" exact component={Home} />
                    <Route path="/addnew" component={Addnew} />
                    <Route path="/edit/:id" component={Edit} />
                   
                    <Route path="*" render={() => <Redirect to={{pathname: "/home"}} />} /> 
                </Switch>
                </Suspense>
            </Router>
        );
  }
}
export default App;

