import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import '@fortawesome/fontawesome-free/resume/ofont.css';
import '@fortawesome/fontawesome-free/hobbie/style.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { createStore } from 'redux';
import { Provider } from 'react-redux'
import { BrowserRouter } from 'react-router-dom'
import {saveState, loadState} from "./localstorage"
import { history } from './helpers';
import { reducer } from './helpers/resumereducer.js';
const checkone = JSON.parse(localStorage.getItem('cartrecord'));
if(checkone == null){
	localStorage.setItem('cartrecord', JSON.stringify([{
    id: "cpgz3oakk",
    title: "Test Title",
    description: "Test Desc",
    date: "2020-11-21",
    time: "16:23",
    status: 'pending',
    created_date: "2020-11-21",
    created_time: "16:23"
  }]));
}
const persistedState = loadState("cartrecord");
const store = createStore(reducer, {cartrecord:persistedState});
store.subscribe(() => saveState("cartrecord", store.getState('cartrecord')));
//localStorage.removeItem('cartrecord');
ReactDOM.render(
    <BrowserRouter history={history}>
        <Provider store={store}>
            <App />
        </Provider>
    </BrowserRouter>,
    document.getElementById('root')
);

serviceWorker.unregister();
